nization } from \"../../../../../../common/qCloudTemp/membership\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport { userInfo } from \"os\";\n\n\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\n\n\nconst routerParams = router.getRouteParams<{ userId: string }>();\nbackofficeUseCase.setBreadcrumb([\"Users\", \"User Detail\"]);\nconst user = ref<IUser_SUSI>();\nconst orgInfo = ref<Array<IOrganization>>([]);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nonBeforeMount(async () => {\n    await getOrganizations();\n})\nfunction viewOrganization(organizationId: string) {\n    router.push(`/backoffice/organization/${organizationId}`);\n}\nfunction remove( userId: string, organizationId: string) {\n    executor.execute(async () => {\n        await backofficeUseCase.removeUserFromOrganization({ userId, organizationId });\n        await getOrganizations();\n    }, { loading: true });\n}\nfunction formatDate(date?: Date) {\n    if(date) {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(date));\n        return formattedDate;\n    }\n}\nfunction openManageUserDialog () {\n  dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/backoffice/manageUser.vue`)),\n        { closable: true, title: \"Manage User\", width: \"499px\", maxHeight: \"184px\" }, {user: user.value}\n    );\n}\nfunction getOrganizations() {\n    orgInfo.value = [];\n    executor.execute(async () => {\n        user.value = await backofficeUseCase.getUserInfo(routerParams.userId);\n        await user.value?.organizationIds.forEach(async (orgId) => {\n            orgInfo.value?.push(await backofficeUseCase.getOrganizationInfo(orgId))\n        })\n    }, { loading: true });\n}\n</script>\n\n<template>\n    <div class=\"manage-user-div\">\n        <button class=\"manage-btn\" @click=\"openManageUserDialog()\"><i class=\"mdi mdi-cog\" style=\"font-size: 20px; margin-right: 4px\"></i><span>MANAGE USER</span></button>\n    </div>  \n    <div style=\"display:flex; margin-bottom: 32px;\">\n        <div class=\"user-info-type\">\n            <div class=\"user-info-type-name\">Name</div>\n            <div class=\"user-info\">{{ user?.name }}</div>\n        </div>\n        <div class=\"user-info-type\">\n            <div class=\"user-info-type-name\">Last Name</div>\n            <div class=\"user-info\">{{ user?.lastName }}</div>\n        </div>\n        <div class=\"user-info-type\">\n            <div class=\"user-info-type-name\">E-mail</div>\n            <div class=\"user-info\">{{ user?.email }}</div>\n        </div>\n        <div class=\"user-info-type\" style=\"flex: 1\">\n            <div class=\"user-info-type-name\">ID</div>\n            <div class=\"user-info\" style=\"font-size: 20px;\">{{ user?.id }}</div>\n        </div>\n        <div class=\"user-info-type\" style=\"flex: 1\">\n            <div class=\"user-info-type-name\">Object ID</div>\n            <div class=\"user-info\" style=\"font-size: 20px;\">{{ user?.objectId }}</div>\n        </div>\n    </div>\n    <div style=\"margin-bottom: 32px;\">\n        <div class=\"div-title\">Organizations ({{ orgInfo.length }})</div>\n        <div>\n            <table class=\"user-org-table\"> \n                <tr class=\"thead\">\n                    <th class=\"col-xl-3\">ID</th>\n                    <th class=\"col-xl-3\">Name</th>\n                    <th class=\"col-xl-3\">Date Created</th>\n                    <th class=\"col-xl-2\">Date Joined</th>\n                    <th class=\"col-xl-1\"></th>\n                </tr>\n                <tr v-for=\"org in orgInfo\" :key=\"org\">\n                    <td>{{ org.id }}</td>\n                    <td>{{ org.name }}</td>\n                    <td> {{formatDate(org.createDate)}}</td>\n                    <td>-</td>\n                    <td><div style=\"display: flex;\">\n                        <button @click=\"remove(user.id, org.id)\" class=\"remove-btn\">REMOVE</button>\n                        <button @click=\"viewOrganization(org.id)\" class=\"view-btn\">VIEW</button>\n                    </div></td>\n                </tr>\n            </table>\n        </div>\n    </div>\n    <div style=\"display: none\">\n        <div class=\"div-title\">Roles ({{ user?.organizationIds.length }})</div>\n        <div>\n            <table class=\"user-org-table\"> \n                <tr class=\"thead\">\n                    <th class=\"col-xl-3\">ID</th>\n                    <th class=\"col-xl-3\">Name</th>\n                    <th class=\"col-xl-3\">Date Created</th>\n                    <th class=\"col-xl-2\">Date Joined</th>\n                    <th class=\"col-xl-1\"></th>\n                </tr>\n                <tr v-for=\"org in orgInfo\" :key=\"org\">\n                    <td>{{ org.id }}</td>\n                    <td>{{ org.name }}</td>\n                    <td> {{ org.createDate }}</td>\n                    <td>-</td>\n                    <td><div style=\"display: flex;\">\n                        <button class=\"remove-btn\">REMOVE</button>\n                        <button class=\"view-btn\">VIEW</button>\n                    </div></td>\n                </tr>\n            </table>\n        </div>\n    </div>\n\n</template>\n\n<style scoped>\n.user-info-type {\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    background: #FFFFFF;\n    margin-right: 37px;\n    padding: 24px 24px 18px;\n    display: flex;\n    flex-direction: column;\n}\n.user-info-type:last-child {\n    margin-right: 0;\n}\n.user-info-type-name {\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    color: #757575;\n    margin-bottom: 20px;\n    min-width: 100px;\n    flex: 1;\n}\n.user-info {\n    font-weight: 500;\n    font-size: 28px;\n    line-height: 33px;\n    color: #151233; \n}\n.div-title {\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    color: #151233;\n    margin-bottom: 20px;\n}\n.user-org-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\ntd:hover {\n    background-color: none !important;\n}\ntable tr:hover {\n    background-color: #EEEEEE;\n}   \n.user-org-table th\n    {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 18px 36px\n}\n.user-org-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 20px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.user-org-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.view-btn {\n    background: none;\n    border:none;\n    color: #2C5A86;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.remove-btn {\n    background: none;\n    border:none;\n    color: #FF6B6B;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.manage-user-div {\n    width: 85%; \n    display: flex; \n    justify-content: end;\n    position: absolute;\n    top: 80px;\n    right: 36px;\n}\n.manage-btn {\n  border: 1px solid #151233;\n  border-radius: 4px;\n  padding: 1.5px 16px;\n  font-size: 15px;\n  color: #151233;\n  display: flex;\n  align-items: center;\n  font-weight: 500;\n}\n</style>"],"sourceRoot":""}]);
